import { io } from 'socket.io-client';

const options = {
  "force new connection": true,
  reconnectionAttempts: "Infinity", // avoid having user reconnect manually in order to prevent dead clients after a server restart
  timeout : 10000, // before connect_error and connect_timeout are emitted.
  transports : ["websocket"],
  secure: true, // Указываем, что соединение безопасное
  rejectUnauthorized: false, // Игнорируем ошибки самоподписанных сертификатов
}

// Замените HTTP на HTTPS и укажите правильный адрес сервера
const socket = io('https://192.168.0.165:3002', options); // Используем HTTPS

export default socket;
